import PressButton from 'press-ui/press-button/press-button.vue';
import PressCell from 'press-ui/press-cell/press-cell.vue';

import PressGridItem from 'press-ui/press-grid-item/press-grid-item.vue';
import PressGrid from 'press-ui/press-grid/press-grid.vue';

import PressPicker from 'press-ui/press-picker/press-picker.vue';
import PressPopover from 'press-ui/press-popover/press-popover.vue';
import PressPopup from 'press-ui/press-popup/press-popup.vue';
import PressSwitch from 'press-ui/press-switch/press-switch.vue';


export default {
  i18n: {
    'zh-CN': {
      actions: [{ text: '选项一' }, { text: '选项二' }, { text: '选项三' }],
      shortActions: [{ text: '选项一' }, { text: '选项二' }],
      actionsWithIcon: [
        { text: '选项一', icon: 'add-o' },
        { text: '选项二', icon: 'music-o' },
        { text: '选项三', icon: 'more-o' },
      ],
      actionsDisabled: [
        { text: '选项一', disabled: true },
        { text: '选项二', disabled: true },
        { text: '选项三' },
      ],
      showIcon: '展示图标',
      placement: '弹出位置',
      darkTheme: '深色风格',
      lightTheme: '浅色风格',
      showPopover: '点击弹出气泡',
      actionOptions: '选项配置',
      customContent: '自定义内容',
      disableAction: '禁用选项',
      choosePlacement: '选择弹出位置',
      useGetContainer: '是否使用 getContainer',
    },
    'en-US': {
      actions: [
        { text: 'Option 1' },
        { text: 'Option 2' },
        { text: 'Option 3' },
      ],
      shortActions: [{ text: 'Option 1' }, { text: 'Option 2' }],
      actionsWithIcon: [
        { text: 'Option 1', icon: 'add-o' },
        { text: 'Option 2', icon: 'music-o' },
        { text: 'Option 3', icon: 'more-o' },
      ],
      actionsDisabled: [
        { text: 'Option 1', disabled: true },
        { text: 'Option 2', disabled: true },
        { text: 'Option 3' },
      ],
      showIcon: 'Show Icon',
      placement: 'Placement',
      darkTheme: 'Dark Theme',
      lightTheme: 'Light Theme',
      showPopover: 'Show Popover',
      actionOptions: 'Action Options',
      customContent: 'Custom Content',
      disableAction: 'Disable Action',
      choosePlacement: 'Placement',
      useGetContainer: 'Use GetContainer',
    },
  },
  components: {
    PressPopover,
    PressButton,
    PressPicker,
    PressPopup,
    PressCell,
    PressGridItem,
    PressGrid,
    PressSwitch,
  },
  data() {
    return {
      show: {
        lightTheme: false,
        darkTheme: false,
        placement: false,
        showIcon: false,
        disableAction: false,
        customContent: false,
      },
      showPicker: false,
      currentPlacement: 'top',
      placements: [
        'top',
        'top-start',
        'top-end',
        'left',
        'left-start',
        'left-end',
        'right',
        'right-start',
        'right-end',
        'bottom',
        'bottom-start',
        'bottom-end',
      ],
      headerStyle: 'background: #f7f8fa;',
      sectionStyle: 'background: #f7f8fa;margin: 0 0 6px;padding: 0;',

      useGetContainer: false,
    };
  },
  computed: {
    getContainer() {
      return this.useGetContainer ? 'body' : '';
    },
  },
  watch: {
    'show.disableAction'() {
      this.onCloseOthers('disableAction');
    },
    'show.lightTheme'() {
      this.onCloseOthers('lightTheme');
    },
    'show.darkTheme'() {
      this.onCloseOthers('darkTheme');
    },
    'show.customContent'() {
      this.onCloseOthers('customContent');
    },
    'show.showIcon'() {
      this.onCloseOthers('showIcon');
    },
  },
  methods: {
    onCloseOthers(key) {
      if (!this.show[key]) return;
      Object.keys(this.show).forEach((item) => {
        if (item !== key) {
          this.show[item] = false;
        }
      });
    },
    onPickerChange({ value, index }) {
      console.log('[onPickerChange]', value, index);
      setTimeout(() => {
        this.show.placement = true;
        this.currentPlacement = value;
      });
    },
    onSelect(action) {
      console.log('[onSelect]', action);
      this.onGTip(action.text);
    },
    onOpen() {
      console.log('onOpen');
    },
    onClose() {
      console.log('onClose');
    },
    onOpened() {
      console.log('onOpened');
    },
    onClosed() {
      console.log('onClosed');
    },
    closeAllPopover() {
      Object.keys(this.show).forEach((key) => {
        this.show[key] = false;
      });
    },
  },
};
